*{
  scroll-behavior: smooth;
}
.app{
  min-height: 100vh;
  background-color: #39445a;
  color: white;
  padding-top: 100px;
  padding-bottom: 70px;
}

@media(max-width: 700px){
  .app{
    padding-top:70px
  }
}

.pageTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
  font-size: 4vw;
  padding: 3px;
  border-radius: 50px;
  color: white;
}

@media (max-width: 1000px) {
  .pageTitle {
    font-size: 3.4vw;
  }
}